const limits = {
  jonas: 1500,
  matilda: 100,
};

const add = function (value, description, user = 'jonas') {
  user = user.toLowerCase();

  let lim;

  limits[user] ? (lim = limits[user]) : (lim = 0);

  // if (limits[user]) {
  //   lim = limits[user];
  // } else {
  //   lim = 0;
  // }

  if (value <= lim) {
    budget.push({ value: -value, description: description, user: user });
  }
};
add(10, 'Pizza üçï');
add(100, 'Going to movies üçø', 'Matilda');
add(200, 'Stuff', 'Jay');
console.log(budget);

var check = function () {
  for (var el of budget) {
    var lim;
    if (limits[el.user]) {
      lim = limits[el.user];
    } else {
      lim = 0;
    }

    if (el.value < -lim) {
      el.flag = 'limit';
    }
  }
};
check();

console.log(budget);

var bigExpenses = function (limit) {
  var output = '';
  for (var el of budget) {
    if (el.value <= -limit) {
      output += el.description.slice(-2) + ' / '; // Emojis are 2 chars
    }
  }
  output = output.slice(0, -2); // Remove last '/ '
  console.log(output);
};
